import React, { useState, useEffect, useMemo } from 'react';
import Form from './components/Form';
import PaymentModal from './components/PaymentModal';
import AnalysisSection from './components/AnalysisSection';
import './App.css';
import { createOrderStream, initiatePayment, fetchAnalysisContent } from './api';

const ANALYSIS_TYPES = [
  { id: 'health', name: 'å¥åº·åˆ†æ' },
  { id: 'career', name: 'äº‹ä¸šåˆ†æ' },
  { id: 'wealth', name: 'è´¢è¿åˆ†æ' },
  { id: 'marriage', name: 'å©šå§»åˆ†æ' }
];

function App() {
  const [analysisData, setAnalysisData] = useState({
    currentOrderId: null,
    preview: '',
    unlocked: {},
    contents: {
      health: null,
      career: null,
      wealth: null,
      marriage: null
    },
    analysisOrderIds: {
      health: null,
      career: null,
      wealth: null,
      marriage: null
    },
    paymentInfo: {
      health: null,
      career: null,
      wealth: null,
      marriage: null
    }
  });
  const [paymentState, setPaymentState] = useState({
    show: false,
    qrcodeUrl: '',
    processingType: null,
    expiresAt: null
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [es, setES] = useState(null);
  const [progress, setProgress] = useState(0);

  const storage = useMemo(() => ({
    save: (data) => {
      if (typeof data !== 'object' || data === null) {
        setError('æ— æ³•ä¿å­˜æ— æ•ˆçš„åˆ†ææ•°æ®');
        return false;
      }
  
      try {
        localStorage.setItem('analysisData', JSON.stringify(data));
        return true;
      } catch (error) {
        setError('ä¿å­˜æ•°æ®å¤±è´¥ï¼Œè¯·æ£€æŸ¥æµè§ˆå™¨å­˜å‚¨ç©ºé—´');
        return false;
      }
    },
  
    load: () => {
      try {
        const data = localStorage.getItem('analysisData');
        if (!data) return null;
  
        const parsedData = JSON.parse(data);
  
        if (
          typeof parsedData === 'object' &&
          parsedData !== null &&
          typeof parsedData.preview === 'string' &&
          typeof parsedData.unlocked === 'object' &&
          typeof parsedData.contents === 'object' &&
          typeof parsedData.currentOrderId === 'string' &&
          typeof parsedData.analysisOrderIds === 'object'
        ) {
          // ç¡®ä¿æ”¯ä»˜ä¿¡æ¯å¯¹è±¡å­˜åœ¨
          if (!parsedData.paymentInfo) {
            parsedData.paymentInfo = { health: null, career: null, wealth: null, marriage: null };
          }
          return parsedData;
        }
        
        throw new Error('æ•°æ®æ ¼å¼ä¸æ­£ç¡®');
      } catch (error) {
        localStorage.removeItem('analysisData');
        return null;
      }
    },

    // æ–°å¢ä¸“é—¨å¤„ç†æ”¯ä»˜ä¿¡æ¯çš„ä¿å­˜å’ŒåŠ è½½æ–¹æ³•
    savePaymentInfo: (type, paymentInfo) => {
      try {
        const key = `payment_${type}`;
        if (paymentInfo === null) {
          localStorage.removeItem(key);
        } else {
          localStorage.setItem(key, JSON.stringify(paymentInfo));
        }
        return true;
      } catch (error) {
        console.error('ä¿å­˜æ”¯ä»˜ä¿¡æ¯å¤±è´¥', error);
        return false;
      }
    },

    loadPaymentInfo: (type) => {
      try {
        const key = `payment_${type}`;
        const data = localStorage.getItem(key);
        if (!data) return null;
        
        return JSON.parse(data);
      } catch (error) {
        localStorage.removeItem(`payment_${type}`);
        return null;
      }
    }
  }), [setError]);

  const initialAnalysisState = {
    currentOrderId: null,
    preview: '',
    unlocked: {},
    contents: { health: '', career: '', wealth: '', marriage: '' },
    analysisOrderIds: { health: null, career: null, wealth: null, marriage: null },
    paymentInfo: { health: null, career: null, wealth: null, marriage: null }
  };

  const clearAnalysisData = () => {
    try {
      localStorage.removeItem('analysisData');
      setAnalysisData(initialAnalysisState);
    } catch (error) {
      setError('æ¸…é™¤æ•°æ®å¤±è´¥');
    }
  };

  const refreshAnalysis = async (type) => {
    try {
      if (!analysisData.currentOrderId || !analysisData.analysisOrderIds[type]) {
        throw new Error('æ— æ•ˆçš„è®¢å•ä¿¡æ¯ï¼Œè¯·é‡æ–°ç”Ÿæˆåˆ†æ');
      }

      setLoading(true);
      const content = await fetchAnalysisContent(
        analysisData.currentOrderId,
        type,
        analysisData.analysisOrderIds[type]
      );

      setAnalysisData(prev => ({
        ...prev,
        contents: {
          ...prev.contents,
          [type]: content
        }
      }));
    } catch (error) {
      setError(error.message || 'åˆ·æ–°å†…å®¹å¤±è´¥');
    } finally {
      setLoading(false);
    }
  };
  
  const handleSubmit = async (eightCharacters) => {
    setLoading(true);
    setError('');
    setAnalysisData(initialAnalysisState);
    setProgress(0); // åˆå§‹åŒ–è¿›åº¦ä¸º0

    // è®¾ç½®ä¸€ä¸ªæ¨¡æ‹Ÿè¿›åº¦çš„å®šæ—¶å™¨
    const progressInterval = setInterval(() => {
      setProgress(prev => {
        // è¿›åº¦æœ€å¤§åˆ°95%ï¼Œç•™5%ç»™å®é™…å®Œæˆ
        if (prev >= 95) {
          clearInterval(progressInterval);
          return 95;
        }
        return prev + 1;
      });
    }, 200); // æ¯200mså¢åŠ 1%ï¼Œå¤§çº¦20ç§’è¾¾åˆ°95%

    const newES = createOrderStream(eightCharacters, {
      onStream: (chunk) => {
        setAnalysisData(prev => ({
          ...prev,
          preview: prev.preview + chunk
        }));
        setLoading(false);
        setProgress(100); // è®¾ç½®ä¸º100%
        clearInterval(progressInterval); // æ¸…é™¤è¿›åº¦å®šæ—¶å™¨
      },
      onComplete: (prevOrderId) => {
        setAnalysisData(prev => ({
          ...prev,
          currentOrderId: prevOrderId
        }));
      },
      onError: (message) => {
        setError(message || 'ç”Ÿæˆé¢„è§ˆå¤±è´¥');
        setLoading(false);
        clearInterval(progressInterval); // æ¸…é™¤è¿›åº¦å®šæ—¶å™¨
      }
    });
    setES(newES);
  };

  const unlockAnalysis = (type) => {
    if (!analysisData.currentOrderId) return;

    // æ£€æŸ¥æ˜¯å¦æœ‰æœªè¿‡æœŸçš„æ”¯ä»˜ä¿¡æ¯ï¼ˆä»ç‹¬ç«‹å­˜å‚¨ä¸­è¯»å–ï¼‰
    const now = Date.now();
    const existingPayment = storage.loadPaymentInfo(type);
    
    if (existingPayment && existingPayment.expiresAt > now) {
      // å°†æ”¯ä»˜ä¿¡æ¯åŒæ­¥åˆ°çŠ¶æ€ä¸­
      setAnalysisData(prev => ({
        ...prev,
        paymentInfo: {
          ...prev.paymentInfo,
          [type]: existingPayment
        },
        analysisOrderIds: {
          ...prev.analysisOrderIds,
          [type]: existingPayment.orderId
        }
      }));
      
      // æ˜¾ç¤ºç°æœ‰çš„æ”¯ä»˜äºŒç»´ç 
      setPaymentState({
        show: true,
        qrcodeUrl: existingPayment.qrcodeUrl,
        processingType: type,
        expiresAt: existingPayment.expiresAt
      });
      return;
    }

    // åˆå§‹åŒ–æ”¯ä»˜çŠ¶æ€
    const initialPaymentState = {
      show: true,
      qrcodeUrl: '',
      processingType: type,
      expiresAt: null
    };
    setPaymentState(initialPaymentState);

    const paymentES = initiatePayment(analysisData.currentOrderId, type, {
      onPaymentQR: (qrcodeUrl, orderId) => {
        const expiresAt = Date.now() + 5 * 60 * 1000;
        
        // åˆ›å»ºæ”¯ä»˜ä¿¡æ¯å¯¹è±¡
        const paymentInfo = {
          qrcodeUrl,
          orderId,
          expiresAt,
          createdAt: Date.now()
        };
        
        // æ›´æ–°çŠ¶æ€
        setPaymentState(prev => ({ 
          ...prev, 
          qrcodeUrl,
          expiresAt 
        }));
        
        setAnalysisData(prev => ({
          ...prev,
          analysisOrderIds: {
            ...prev.analysisOrderIds,
            [type]: orderId
          },
          paymentInfo: {
            ...prev.paymentInfo,
            [type]: paymentInfo
          }
        }));
        
        // ç‹¬ç«‹ä¿å­˜æ”¯ä»˜ä¿¡æ¯
        storage.savePaymentInfo(type, paymentInfo);
      },
      onAnalysisChunk: (chunk) => {
        setAnalysisData(prev => ({
          ...prev,
          contents: {
            ...prev.contents,
            [type]: (prev.contents[type] || '') + chunk
          }
        }));
      },
      onPaymentstatus: () => {
        // æ”¯ä»˜æˆåŠŸï¼Œæ›´æ–°çŠ¶æ€å¹¶æ¸…é™¤æ”¯ä»˜ä¿¡æ¯
        setAnalysisData(prev => ({
          ...prev,
          unlocked: { ...prev.unlocked, [type]: true },
          paymentInfo: { ...prev.paymentInfo, [type]: null }
        }));
        setPaymentState({ show: false, qrcodeUrl: '', processingType: null, expiresAt: null });
        
        // æ¸…é™¤ç‹¬ç«‹å­˜å‚¨çš„æ”¯ä»˜ä¿¡æ¯
        storage.savePaymentInfo(type, null);
      },
      onAnalysisEnd: () => {
        // åˆ†æå®Œæˆåçš„å¤„ç†
      },
      onError: (message) => {
        setError(message || 'æ”¯ä»˜æµç¨‹å‡ºé”™');
        setPaymentState({ show: false, qrcodeUrl: '', processingType: null, expiresAt: null });
      }
    });

    setES(paymentES);
  };

  useEffect(() => {
    if (analysisData.preview) {
      storage.save(analysisData);
    }
  }, [analysisData, storage]);

  useEffect(() => {
    const loadedData = storage.load();
    if (loadedData) {
      setAnalysisData(loadedData);
    }

    return () => {
      if (es) {
        es.close();
      }
    };
  }, [es, storage]);

  // è¯»å–é¡µé¢åˆå§‹åŒ–æ—¶çš„æ”¯ä»˜ä¿¡æ¯
  useEffect(() => {
    // åŠ è½½æ‰€æœ‰ç±»å‹çš„æ”¯ä»˜ä¿¡æ¯
    ANALYSIS_TYPES.forEach(type => {
      const paymentInfo = storage.loadPaymentInfo(type.id);
      if (paymentInfo) {
        // æ£€æŸ¥æ˜¯å¦è¿‡æœŸ
        if (paymentInfo.expiresAt > Date.now()) {
          setAnalysisData(prev => ({
            ...prev,
            paymentInfo: {
              ...prev.paymentInfo,
              [type.id]: paymentInfo
            },
            analysisOrderIds: {
              ...prev.analysisOrderIds,
              [type.id]: paymentInfo.orderId
            }
          }));
        } else {
          // è¿‡æœŸåˆ™æ¸…é™¤
          storage.savePaymentInfo(type.id, null);
        }
      }
    });
  }, [storage]);

  // æ›¿æ¢åŸæœ‰çš„å®šæ—¶æ¸…ç†å‡½æ•°ï¼Œä½¿ç”¨æ›´å¯é çš„æ–¹å¼
  useEffect(() => {
    const cleanupInterval = setInterval(() => {
      const now = Date.now();
      
      ANALYSIS_TYPES.forEach(type => {
        const paymentInfo = storage.loadPaymentInfo(type.id);
        if (paymentInfo && paymentInfo.expiresAt < now) {
          // æ”¯ä»˜ä¿¡æ¯å·²è¿‡æœŸï¼Œæ¸…é™¤
          storage.savePaymentInfo(type.id, null);
          
          // åŒæ—¶æ›´æ–°çŠ¶æ€
          setAnalysisData(prev => ({
            ...prev,
            paymentInfo: {
              ...prev.paymentInfo,
              [type.id]: null
            }
          }));
        }
      });
    }, 30000); // æ¯30ç§’æ£€æŸ¥ä¸€æ¬¡
    
    return () => clearInterval(cleanupInterval);
  }, [storage]);

  return (
    <div className="App">
      <header className="header">
        <div className="header-content">
          <div className="branding">
            <h1>MingLiç†å‘½</h1>
            <p className="slogan">ä¸€é˜´ä¸€é˜³ä¹‹è°“é“</p>
          </div>
          <div className="logo-container">
            <img src="/logo.png" alt="Logo" className="logo" />
          </div>
        </div>
      </header>

        {error && <div className="error-message">{error}</div>}
        
        {loading ? (
            <div className="loading-wrapper">
            <div className="loading">
              <div className="loading-text">
                â³ æ·±åº¦åˆ†æé€šå¸¸éœ€è¦ 30 ç§’
                <div className="sub-hint">ï¼ˆæœŸé—´è¯·ä¸è¦åˆ·æ–°é¡µé¢ï¼‰</div>
              </div>
              <div className="progress-container">
                <div 
                  className="progress-bar" 
                  style={{ width: `${progress}%` }}
                ></div>
              </div>
              <div className="progress-percentage">{progress}%</div>
            </div>
          </div>
        ) : (
          !analysisData.preview ? (
            <Form onSubmit={handleSubmit} />
          ) : (
            <AnalysisSection
              preview={analysisData.preview}
              unlocked={analysisData.unlocked}
              contents={analysisData.contents}
              onUnlock={unlockAnalysis}
              onRefresh={refreshAnalysis}
              analysisTypes={ANALYSIS_TYPES}
              onGoBack={clearAnalysisData}
              onComplete={analysisData.currentOrderId}
            />
          )
        )}

        <PaymentModal
          isOpen={paymentState.show}
          qrcodeUrl={paymentState?.qrcodeUrl}
          onClose={() => setPaymentState({ show: false, qrcodeUrl: '', processingType: null, expiresAt: null })}
          expiresAt={paymentState.expiresAt}
        />
    
      <footer>
        <div className="sfooterContent">
          <p className="sdisclaimer">
            æ‹’ç»å‘æœªæˆå¹´äººæä¾›æœåŠ¡ï¼Œä»…ä¾›å¨±ä¹å’Œå‚è€ƒã€‚
          </p>
          <p className="sdisclaimer">
          Â©2025 MingLiç†å‘½
          </p>
        </div>
      </footer>

    </div>
    
  );
}

export default App;



