import ReactMarkdown from "react-markdown";
import React, { useRef, useEffect, useState, useCallback } from 'react';
import html2canvas from 'html2canvas';
import './AnalysisSection.css';

const ANALYSIS_TYPES = [ 
  { id: 'health', name: 'å¥åº·åˆ†æ', icon: 'â¤ï¸', color: '#ED7D31' },
  { id: 'career', name: 'äº‹ä¸šåˆ†æ', icon: 'ğŸ’¼', color: '#005293' },
  { id: 'wealth', name: 'è´¢è¿åˆ†æ', icon: 'ğŸ’°', color: '#8064A6' },
  { id: 'marriage', name: 'å©šå§»åˆ†æ', icon: 'ğŸ’', color: '#A3CE27' }
];

const AnalysisSection = ({ preview, unlocked, contents, onUnlock, onRefresh, onGoBack, onComplete }) => {
  const [displayContent, setDisplayContent] = useState('');
  const [blurContent, setBlurContent] = useState('');
  const [selectedType, setSelectedType] = useState(null);
  const [loadingType, setLoadingType] = useState(null);
  const [isShared, setIsShared] = useState(false);
  const [isCopied, setIsCopied] = useState(false);
  const [error, setError] = useState('');
  const [analysisProgress, setAnalysisProgress] = useState({
    health: 0,
    career: 0,
    wealth: 0,
    marriage: 0
  });
  const [generatingTypes, setGeneratingTypes] = useState({
    health: false,
    career: false, 
    wealth: false,
    marriage: false
  });
  const [contentStuck, setContentStuck] = useState({
    health: false,
    career: false, 
    wealth: false,
    marriage: false
  });
  const progressIntervals = useRef({});
  const contentUpdateRef = useRef({
    health: false,
    career: false, 
    wealth: false,
    marriage: false
  });
  const stuckTimers = useRef({});

  const handleRefresh = useCallback((type) => {
    onRefresh(type);
    setContentStuck(prev => ({
      ...prev,
      [type]: false
    }));
  }, [onRefresh]);

  const handleUnlock = useCallback(async (typeId) => {
    if (unlocked[typeId] || loadingType) return;
    
    try {
      setLoadingType(typeId);
      setAnalysisProgress(prev => ({
        ...prev,
        [typeId]: 0
      }));
      
      setGeneratingTypes(prev => ({
        ...prev,
        [typeId]: true
      }));
      
      if (progressIntervals.current[typeId]) {
        clearInterval(progressIntervals.current[typeId]);
      }
      
      const interval = setInterval(() => {
        setAnalysisProgress(prevProgress => {
          const currentProgress = prevProgress[typeId];
          
          if (contentUpdateRef.current[typeId] || currentProgress >= 95) {
            clearInterval(interval);
            return prevProgress;
          }
          
          return {
            ...prevProgress,
            [typeId]: Math.min(currentProgress + 1, 95)
          };
        });
      }, 300);
      
      progressIntervals.current[typeId] = interval;
      
      await onUnlock(typeId);
      setSelectedType(typeId);
    } catch (error) {
      setError('è§£é”å¤±è´¥ï¼Œè¯·é‡è¯•');
      clearInterval(progressIntervals.current[typeId]);
      setAnalysisProgress(prev => ({
        ...prev,
        [typeId]: 0
      }));
      setGeneratingTypes(prev => ({
        ...prev,
        [typeId]: false
      }));
      setLoadingType(null);
    }
  }, [unlocked, loadingType, onUnlock]);

  const handleTypeSelection = useCallback((typeId) => {
    if (unlocked[typeId]) {
      setSelectedType(typeId);
    } else {
      handleUnlock(typeId);
    }
  }, [unlocked, handleUnlock]);
  
  const contentRef = useRef(null);
  const watermarkImage = useRef(new Image());
  
  useEffect(() => {
    watermarkImage.current.src = '/qrcode.png';
  }, []);

  useEffect(() => {
    const typesToUpdate = [];
    
    Object.keys(contents).forEach(type => {
      if (contents[type] && !contentUpdateRef.current[type] && generatingTypes[type]) {
        typesToUpdate.push(type);
        contentUpdateRef.current[type] = true;
      }
    });
    
    if (typesToUpdate.length > 0) {
      setAnalysisProgress(prev => {
        const newProgress = {...prev};
        typesToUpdate.forEach(type => {
          newProgress[type] = 100;
        });
        return newProgress;
      });
      
      setGeneratingTypes(prev => {
        const newGenerating = {...prev};
        typesToUpdate.forEach(type => {
          newGenerating[type] = false;
        });
        return newGenerating;
      });
      
      if (typesToUpdate.includes(loadingType)) {
        setLoadingType(null);
      }
      
      typesToUpdate.forEach(type => {
        if (progressIntervals.current[type]) {
          clearInterval(progressIntervals.current[type]);
          progressIntervals.current[type] = null;
        }
      });
    }
  }, [contents, loadingType, generatingTypes]);

  useEffect(() => {
    // è¿™ä¸ªeffectéœ€è¦æ•è·å½“å‰çš„çŠ¶æ€å’Œrefï¼Œç¡®ä¿æ¸…ç†å‡½æ•°ä½¿ç”¨æ­£ç¡®çš„å€¼
    const currentSelectedType = selectedType;
    const currentGeneratingTypes = generatingTypes;
    const currentContentStuck = contentStuck;
    
    if (currentSelectedType && currentGeneratingTypes[currentSelectedType] && !currentContentStuck[currentSelectedType]) {
      if (stuckTimers.current[currentSelectedType]) {
        clearTimeout(stuckTimers.current[currentSelectedType]);
      }
      
      const timer = setTimeout(() => {
        if (currentGeneratingTypes[currentSelectedType]) {
          setContentStuck(prev => ({
            ...prev,
            [currentSelectedType]: true
          }));
        }
      }, 30000);
      
      stuckTimers.current[currentSelectedType] = timer;
      
      // å­˜å‚¨å½“å‰çš„timerå¼•ç”¨
      const currentTimer = timer;
      
      return () => {
        // ä½¿ç”¨æ•è·çš„å¼•ç”¨è¿›è¡Œæ¸…ç†
        clearTimeout(currentTimer);
      };
    }
  }, [selectedType, generatingTypes, contentStuck]);

  useEffect(() => {
    // ä¿å­˜å¯¹å½“å‰intervalså’Œtimersçš„å¼•ç”¨ï¼Œä»¥åœ¨æ¸…ç†å‡½æ•°ä¸­ä½¿ç”¨
    const currentProgressIntervals = { ...progressIntervals.current };
    const currentStuckTimers = { ...stuckTimers.current };
    
    return () => {
      // ä½¿ç”¨ä¿å­˜çš„å¼•ç”¨æ¥æ¸…ç†
      Object.values(currentProgressIntervals).forEach(interval => {
        if (interval) clearInterval(interval);
      });
      
      Object.values(currentStuckTimers).forEach(timer => {
        if (timer) clearTimeout(timer);
      });
    };
  }, []);

  useEffect(() => {
    if (!preview) {
      ANALYSIS_TYPES.forEach(type => {
        contentUpdateRef.current[type.id] = false;
      });
    }
  }, [preview]);

  const handleShare = async () => {
    try {
      await navigator.clipboard.writeText(window.location.href);
      setIsCopied(true);
      
      setTimeout(async () => {
        try {
          const hasShared = await window.confirm(
            'é“¾æ¥å·²å¤åˆ¶ï¼\n\nè¯·å°†é“¾æ¥åˆ†äº«åˆ°ç¤¾äº¤å¹³å°\næ˜¯å¦å·²å®Œæˆåˆ†äº«ï¼Ÿ'
          );
          if (hasShared) {
            setIsShared(true);
          }
        } catch (err) {
          setError('åˆ†äº«ç¡®è®¤å¤±è´¥');
        }
      }, 1000);

      setTimeout(() => setIsCopied(false), 3000);
    } catch (err) {
      setError('å¤åˆ¶é“¾æ¥å¤±è´¥ï¼Œè¯·æ‰‹åŠ¨å¤åˆ¶åœ°å€æ é“¾æ¥');
      setTimeout(async () => {
        try {
          const hasShared = await window.confirm(
            'è¯·å°†é“¾æ¥åˆ†äº«åˆ°ç¤¾äº¤å¹³å°\næ˜¯å¦å·²å®Œæˆåˆ†äº«ï¼Ÿ'
          );
          if (hasShared) {
            setIsShared(true);
          }
        } catch (err) {
          setError('åˆ†äº«ç¡®è®¤å¤±è´¥');
        }
      }, 1000);
    }
  };

  const handleScreenshot = async () => {
    try {
      if (!contentRef.current) {
        throw new Error('å†…å®¹åŒºåŸŸæœªå‡†å¤‡å°±ç»ª');
      }

      const canvas = await html2canvas(contentRef.current, {
        useCORS: true,
        allowTaint: true,
        logging: false,
        backgroundColor: '#ffffff'
      });

      const watermarkCanvas = document.createElement('canvas');
      const ctx = watermarkCanvas.getContext('2d');
      
      watermarkCanvas.width = canvas.width;
      watermarkCanvas.height = canvas.height;
      
      ctx.drawImage(canvas, 0, 0);
      
      ctx.fillStyle = '#FF0000';
      ctx.font = 'bold 48px Microsoft YaHei';
      ctx.textAlign = 'center';
      ctx.textBaseline = 'top';
      ctx.fillText('ğŸŒŸ æœ¬æŠ¥å‘Šç”±æ·±æ™ºAIå‘½ç†å¼•æ“ç”Ÿæˆ | www.mingli.xin', canvas.width / 2, 30);

      if (watermarkImage.current.complete) {
        ctx.globalAlpha = 0.8;
        ctx.drawImage(
          watermarkImage.current,
          canvas.width - 300,
          100,
          284,
          318
        );
      }

      const finalUrl = watermarkCanvas.toDataURL('image/png');
      
      const fileName = `å‘½ç†åˆ†ææŠ¥å‘Š_${new Date().toLocaleDateString()}.png`;
      const link = document.createElement('a');
      link.href = finalUrl;
      link.download = fileName;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);

    } catch (error) {
      setError('ç”ŸæˆæŠ¥å‘Šå¤±è´¥ï¼Œè¯·ç¨åé‡è¯•');
    }
  };
  
  useEffect(() => {
    if (typeof preview === 'string' && preview.length > 0) {
      const splitIndex = Math.floor(preview.length * 0.9);
      setDisplayContent(preview.slice(0, splitIndex));
      setBlurContent(preview.slice(splitIndex));
    }
  }, [preview]);

  return (
    <div className="analysis-container" ref={contentRef}>
      {error && (
        <div className="error-message" onClick={() => setError('')}>
          âŒ {error}
        </div>
      )}

      <h2>åŸºç¡€å‘½ç†åˆ†æ</h2>

      {!onComplete ? (
        <ReactMarkdown>{preview}</ReactMarkdown>
      ) : (
        <div className="preview-section">
          <ReactMarkdown>{displayContent}</ReactMarkdown>
          {blurContent && (
            <div className={`blur-section ${(selectedType || isShared) ? 'unlocked' : 'locked'}`}>
              <ReactMarkdown>{blurContent}</ReactMarkdown>
            </div>
          )}
        </div>
      )}

      {onComplete && (
        <>
          <div className="share-section">
            {!(selectedType || isShared) && (
              <div className="share-container">
                <button 
                  onClick={handleShare}
                  className="share-trigger"
                >
                  ğŸ”— å¤åˆ¶é“¾æ¥åˆ†äº«è§£é”
                </button>
                <span className="tooltip">åˆ†äº«ç»™å¾®ä¿¡å¥½å‹å³å¯æŸ¥çœ‹å®Œæ•´è§£æ</span>
              </div>
            )}
            
            {isCopied && (
              <div className="copied-notice">
                âœ… é“¾æ¥å·²å¤åˆ¶åˆ°å‰ªè´´æ¿ï¼
              </div>
            )}
          </div>

          <div className="analysis-modules">
            {ANALYSIS_TYPES.map((type) => (
              <div
                key={type.id}
                className={`analysis-module ${
                  unlocked[type.id] ? 'unlocked' : 'locked'
                } ${selectedType === type.id ? 'active' : ''}`}
                style={{ backgroundColor: type.color }}
                onClick={() => handleTypeSelection(type.id)}
              >
                <div className="module-header">
                  <span className="icon">{type.icon}</span>
                  <h3>{type.name}</h3>
                </div>
                <div className="module-status">
                  {loadingType === type.id || generatingTypes[type.id] ? (
                    analysisProgress[type.id] === 0 ? (
                      <div className="progress-text">å¤„ç†ä¸­...</div>
                    ) : (
                      <>
                        <div className="analysis-progress-container">
                          <div 
                            className="analysis-progress-bar" 
                            style={{ width: `${analysisProgress[type.id]}%` }}
                          ></div>
                        </div>
                        <div className="analysis-progress-percentage">{analysisProgress[type.id]}%</div>
                      </>
                    )
                  ) : unlocked[type.id] ? (
                    contents[type.id] ? 'âœ“ å·²è§£é”' : <div className="loader" />
                  ) : (
                    'ï¿¥0.99è§£é”'
                  )}
                </div>
              </div>
            ))}
          </div>

          {selectedType && contents[selectedType] && (
            <div className="detail-content">
              <div className="detail-header">
                <h3>{ANALYSIS_TYPES.find(type => type.id === selectedType)?.name}</h3>
                {contentStuck[selectedType] && (
                  <button
                    className="refresh-button"
                    onClick={() => handleRefresh(selectedType)}
                  >
                    ğŸ”„ å¡é¡¿åˆ·æ–°
                  </button>
                )}
              </div>
              <ReactMarkdown>{contents[selectedType]}</ReactMarkdown>
            </div>
          )}
        </>
      )}
      <button className="action-button back" onClick={onGoBack}>
        â† è¿”å›ä¸»é¡µ
      </button>
      <button className="action-button screenshot" onClick={handleScreenshot}>
        ğŸ“¸ æˆªå›¾ä¿å­˜
      </button>

      <div className="disclaimer">
        <p>â€» æœ¬åˆ†æç”±AIç”Ÿæˆï¼Œä»…ä¾›å¨±ä¹å‚è€ƒï¼Œä¸ä½œä¸ºå†³ç­–ä¾æ®</p>
        <p>â€» æ ¹æ®ç›¸å…³æ³•è§„ï¼Œæ‰€æœ‰æ•°æ®å°†åœ¨24å°æ—¶åè‡ªåŠ¨åˆ é™¤</p>
      </div>
    </div>
  );
};

const propsAreEqual = (prevProps, nextProps) => {
  return (
    prevProps.preview === nextProps.preview &&
    prevProps.onComplete === nextProps.onComplete &&
    JSON.stringify(prevProps.unlocked) === JSON.stringify(nextProps.unlocked) &&
    JSON.stringify(prevProps.contents) === JSON.stringify(nextProps.contents)
  );
};

export default React.memo(AnalysisSection, propsAreEqual);
